/* axios instance  
   defaultApi.get(API, body);
*/
import axios from 'axios';
import { getCookie } from 'utils/Cookie';
import requests from './config';

const BASE_URL = requests.base_url;

const axiosApi = (url, options) => {
  const instance = axios.create({
    baseURL: url, //URL 
    ...options, //  
  });

  return instance;
};

//   
export const defaultApi = axiosApi(BASE_URL);

//    
export const authApi = axiosApi(BASE_URL);

//refreshToken   
export const refreshTokenApi = axiosApi(BASE_URL);

authApi.interceptors.request.use(
  //  
  (config) => {
    const accessToken = localStorage.getItem('accessToken'); // accessToken 
    if (accessToken) {
      config.headers.Authorization = `Bearer ${accessToken}`;
    }
    return config;
  },
  //   
  (error) => {
    console.log(error);
    return Promise.reject(error);
  },
);

authApi.interceptors.response.use(
  (response) => response,
  (error) => {
    const {
      config,
      response: { status },
    } = error; // 
    const userId = localStorage.getItem('userId');
    const refreshToken = getCookie('refreshToken');

    //Unauthorized
    if (status === 401) {
      //accessToken  
      // console.log(userId, refreshToken);
      //Api  
      return refreshTokenApi
        .post('/api/auth/reissue', {
          userId: userId,
          refreshToken: refreshToken,
        })
        .then((response) => {
          const { accessToken } = response.data;
          // console.log(data);
          localStorage.setItem('accessToken', accessToken);
          config.headers.Authorization = `Bearer ${accessToken}`;

          //    
          // console.log(config);
          return authApi(config);
        });
    }
  },
);
